"use client"
import { useState, useRef, useEffect } from 'react'
import { RotateCcw, ArrowLeft, ArrowRightLeft, ArrowRight, ArrowLeftRight } from 'lucide-react'

// ----------------------
// Toggle Component
// ----------------------
function Toggle({ checked, onChange }) {
  return (
    <button
      onClick={() => onChange(!checked)}
      className={`relative w-12 h-6 rounded-full transition-colors ${
        checked ? 'bg-blue-600' : 'bg-slate-400'
      }`}
      aria-label="Toggle option"
    >
      <span
        className={`absolute top-1 left-1 w-4 h-4 bg-white rounded-full shadow transition-transform ${
          checked ? 'translate-x-6' : 'translate-x-0'
        }`}
      />
    </button>
  )
}

// ----------------------
// Diff Algorithm
// ----------------------
function computeLineDiff(text1, text2) {
  const lines1 = text1.split('\n')
  const lines2 = text2.split('\n')
  
  const maxLen = Math.max(lines1.length, lines2.length)
  const result = []
  
  for (let i = 0; i < maxLen; i++) {
    const line1 = lines1[i] !== undefined ? lines1[i] : null
    const line2 = lines2[i] !== undefined ? lines2[i] : null
    
    if (line1 === null && line2 !== null) {
      result.push({ left: null, right: line2, type: 'added' })
    } else if (line1 !== null && line2 === null) {
      result.push({ left: line1, right: null, type: 'removed' })
    } else if (line1 === line2) {
      result.push({ left: line1, right: line2, type: 'unchanged' })
    } else {
      result.push({ left: line1, right: line2, type: 'modified' })
    }
  }
  
  return result
}

function computeWordDiff(text1, text2) {
  if (!text1 && !text2) return { left: '', right: '' }
  if (!text1) return { 
    left: '', 
    right: `<span class="bg-green-300 dark:bg-green-700">${escapeHtml(text2)}</span>` 
  }
  if (!text2) return { 
    left: `<span class="bg-red-300 dark:bg-red-700">${escapeHtml(text1)}</span>`, 
    right: '' 
  }
  
  const words1 = text1.split(/(\s+)/)
  const words2 = text2.split(/(\s+)/)
  
  let leftHtml = ''
  let rightHtml = ''
  
  const maxLen = Math.max(words1.length, words2.length)
  
  for (let i = 0; i < maxLen; i++) {
    const w1 = words1[i]
    const w2 = words2[i]
    
    if (w1 === w2) {
      leftHtml += escapeHtml(w1 || '')
      rightHtml += escapeHtml(w2 || '')
    } else {
      if (w1) leftHtml += `<span class="bg-red-300 dark:bg-red-700">${escapeHtml(w1)}</span>`
      if (w2) rightHtml += `<span class="bg-green-300 dark:bg-green-700">${escapeHtml(w2)}</span>`
    }
  }
  
  return { left: leftHtml, right: rightHtml }
}

function escapeHtml(text) {
  const div = document.createElement('div')
  div.textContent = text
  return div.innerHTML
}

export default function TextCompare() {
  const [text1, setText1] = useState('')
  const [text2, setText2] = useState('')
  const [compared, setCompared] = useState(false)
  const [diffResult, setDiffResult] = useState(null)
  const [compareMode, setCompareMode] = useState('line') // 'line' or 'word'
  
  const [removeExtraLines1, setRemoveExtraLines1] = useState(false)
  const [removeSpaces1, setRemoveSpaces1] = useState(false)
  const [removeExtraLines2, setRemoveExtraLines2] = useState(false)
  const [removeSpaces2, setRemoveSpaces2] = useState(false)
  
  const [selectedLines, setSelectedLines] = useState(new Set())

  // ----------------------
  // Clean text
  // ----------------------
  const cleanText = (text, removeLines, removeSpaces) => {
    let result = text

    if (removeLines) {
      result = result
        .split('\n')
        .filter((line) => line.trim() !== '')
        .join('\n')
    }

    if (removeSpaces) {
      result = result
        .split('\n')
        .map((line) => line.trim())
        .join('\n')
    }

    return result
  }

  const getCleanedText1 = () => cleanText(text1, removeExtraLines1, removeSpaces1)
  const getCleanedText2 = () => cleanText(text2, removeExtraLines2, removeSpaces2)

  // ----------------------
  // Compare
  // ----------------------
  const compareTexts = () => {
    if (!text1 && !text2) return

    const clean1 = getCleanedText1()
    const clean2 = getCleanedText2()

    if (compareMode === 'line') {
      const lineDiff = computeLineDiff(clean1, clean2)
      setDiffResult(lineDiff)
    } else {
      // Word mode: compare entire text as one block
      const wordDiff = computeWordDiff(clean1, clean2)
      setDiffResult([{
        left: clean1,
        right: clean2,
        type: 'word-comparison',
        wordDiff
      }])
    }

    setCompared(true)
    setSelectedLines(new Set())
  }

  // ----------------------
  // Swap & Reset
  // ----------------------
  const swapTexts = () => {
    const temp = text1
    setText1(text2)
    setText2(temp)
    setCompared(false)
    setDiffResult(null)
    setSelectedLines(new Set())
  }

  const handleReset = () => {
    setText1('')
    setText2('')
    setDiffResult(null)
    setCompared(false)
    setSelectedLines(new Set())
  }

  // ----------------------
  // Line Selection & Merge
  // ----------------------
  const toggleLineSelection = (index) => {
    setSelectedLines(prev => {
      const newSet = new Set(prev)
      if (newSet.has(index)) {
        newSet.delete(index)
      } else {
        newSet.add(index)
      }
      return newSet
    })
  }

  const mergeSelectedToLeft = () => {
    if (!diffResult || selectedLines.size === 0) return

    const lines1 = text1.split('\n')
    const lines2 = text2.split('\n')

    selectedLines.forEach(idx => {
      if (idx < lines2.length) {
        lines1[idx] = lines2[idx]
      }
    })

    setText1(lines1.join('\n'))
    setCompared(false)
    setDiffResult(null)
    setSelectedLines(new Set())
  }

  const mergeSelectedToRight = () => {
    if (!diffResult || selectedLines.size === 0) return

    const lines1 = text1.split('\n')
    const lines2 = text2.split('\n')

    selectedLines.forEach(idx => {
      if (idx < lines1.length) {
        lines2[idx] = lines1[idx]
      }
    })

    setText2(lines2.join('\n'))
    setCompared(false)
    setDiffResult(null)
    setSelectedLines(new Set())
  }

  const mergeSingleLine = (index, direction) => {
    const lines1 = text1.split('\n')
    const lines2 = text2.split('\n')

    if (direction === 'toLeft' && index < lines2.length) {
      lines1[index] = lines2[index]
      setText1(lines1.join('\n'))
    } else if (direction === 'toRight' && index < lines1.length) {
      lines2[index] = lines1[index]
      setText2(lines2.join('\n'))
    }

    setCompared(false)
    setDiffResult(null)
  }

  // ----------------------
  // Count statistics
  // ----------------------
  const getStatistics = () => {
    if (!diffResult || compareMode === 'word') return { removed: 0, added: 0, unchanged: 0, modified: 0 }
    
    let removed = 0, added = 0, unchanged = 0, modified = 0
    
    diffResult.forEach(row => {
      if (row.type === 'removed') removed++
      else if (row.type === 'added') added++
      else if (row.type === 'unchanged') unchanged++
      else if (row.type === 'modified') modified++
    })
    
    return { removed, added, unchanged, modified }
  }

  // ----------------------
  // Render Comparison
  // ----------------------
  const renderComparison = () => {
    if (!diffResult) return null

    if (compareMode === 'word') {
      const wordDiff = diffResult[0].wordDiff
      return (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <div>
            <h3 className="text-lg font-semibold text-slate-900 dark:text-white mb-3">
              Text 1
            </h3>
            <div className="bg-slate-50 dark:bg-slate-950 border border-slate-300 dark:border-slate-700 rounded-lg p-4 min-h-64 font-mono text-sm leading-relaxed break-words whitespace-pre-wrap">
              <div dangerouslySetInnerHTML={{ __html: wordDiff.left || '<span class="text-slate-400">Empty</span>' }} />
            </div>
          </div>

          <div>
            <h3 className="text-lg font-semibold text-slate-900 dark:text-white mb-3">
              Text 2
            </h3>
            <div className="bg-slate-50 dark:bg-slate-950 border border-slate-300 dark:border-slate-700 rounded-lg p-4 min-h-64 font-mono text-sm leading-relaxed break-words whitespace-pre-wrap">
              <div dangerouslySetInnerHTML={{ __html: wordDiff.right || '<span class="text-slate-400">Empty</span>' }} />
            </div>
          </div>
        </div>
      )
    }

    // Line-by-line comparison
    return (
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Left column */}
        <div>
          <h3 className="text-lg font-semibold text-slate-900 dark:text-white mb-3">
            Text 1
          </h3>
          <div className="bg-slate-50 dark:bg-slate-950 border border-slate-300 dark:border-slate-700 rounded-lg p-4 min-h-64 font-mono text-sm leading-relaxed">
            {diffResult.map((row, idx) => {
              const isSelected = selectedLines.has(idx)
              const bgColor = row.type === 'removed' ? 'bg-red-100 dark:bg-red-900/30' :
                              row.type === 'modified' ? 'bg-yellow-100 dark:bg-yellow-900/30' :
                              row.type === 'added' ? 'bg-slate-100 dark:bg-slate-800' :
                              ''
              
              return (
                <div 
                  key={idx} 
                  className={`flex items-center gap-2 py-1 px-2 ${bgColor} ${isSelected ? 'ring-2 ring-blue-500' : ''} hover:bg-opacity-80 transition-colors`}
                >
                  <input
                    type="checkbox"
                    checked={isSelected}
                    onChange={() => toggleLineSelection(idx)}
                    className="flex-shrink-0"
                  />
                  <span className="inline-block min-w-[3ch] text-right text-slate-400 dark:text-slate-500 select-none">
                    {idx + 1}
                  </span>
                  <span className="flex-1 text-slate-700 dark:text-slate-300">
                    {row.left !== null ? row.left : <span className="text-slate-400 italic">empty</span>}
                  </span>
                  {row.type !== 'unchanged' && (
                    <button
                      onClick={() => mergeSingleLine(idx, 'toLeft')}
                      className="flex-shrink-0 p-1 text-blue-600 hover:bg-blue-100 dark:hover:bg-blue-900 rounded transition-colors"
                      title="Copy from Text 2"
                    >
                      <ArrowLeft className="w-4 h-4" />
                    </button>
                  )}
                </div>
              )
            })}
          </div>
        </div>

        {/* Right column */}
        <div>
          <h3 className="text-lg font-semibold text-slate-900 dark:text-white mb-3">
            Text 2
          </h3>
          <div className="bg-slate-50 dark:bg-slate-950 border border-slate-300 dark:border-slate-700 rounded-lg p-4 min-h-64 font-mono text-sm leading-relaxed">
            {diffResult.map((row, idx) => {
              const isSelected = selectedLines.has(idx)
              const bgColor = row.type === 'added' ? 'bg-green-100 dark:bg-green-900/30' :
                              row.type === 'modified' ? 'bg-yellow-100 dark:bg-yellow-900/30' :
                              row.type === 'removed' ? 'bg-slate-100 dark:bg-slate-800' :
                              ''
              
              return (
                <div 
                  key={idx} 
                  className={`flex items-center gap-2 py-1 px-2 ${bgColor} ${isSelected ? 'ring-2 ring-blue-500' : ''} hover:bg-opacity-80 transition-colors`}
                >
                  {row.type !== 'unchanged' && (
                    <button
                      onClick={() => mergeSingleLine(idx, 'toRight')}
                      className="flex-shrink-0 p-1 text-green-600 hover:bg-green-100 dark:hover:bg-green-900 rounded transition-colors"
                      title="Copy from Text 1"
                    >
                      <ArrowRight className="w-4 h-4" />
                    </button>
                  )}
                  <span className="inline-block min-w-[3ch] text-right text-slate-400 dark:text-slate-500 select-none">
                    {idx + 1}
                  </span>
                  <span className="flex-1 text-slate-700 dark:text-slate-300">
                    {row.right !== null ? row.right : <span className="text-slate-400 italic">empty</span>}
                  </span>
                  <input
                    type="checkbox"
                    checked={isSelected}
                    onChange={() => toggleLineSelection(idx)}
                    className="flex-shrink-0"
                  />
                </div>
              )
            })}
          </div>
        </div>
      </div>
    )
  }

  // ----------------------
  // UI
  // ----------------------
  const stats = getStatistics()

  return (
    <main className="min-h-screen bg-gradient-to-br from-white to-slate-50 dark:from-slate-950 dark:to-slate-900 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="mb-8">
          <h1 className="text-3xl md:text-4xl font-bold text-slate-900 dark:text-white mb-2">
            Text Compare Tool
          </h1>
          <p className="text-lg text-slate-600 dark:text-slate-400">
            Compare two texts line-by-line or word-by-word with advanced merging
          </p>
        </div>

        {/* Comparison Mode */}
        <div className="bg-white dark:bg-slate-900 rounded-lg border border-slate-200 dark:border-slate-800 p-6 mb-6 shadow-sm">
          <h2 className="text-lg font-semibold text-slate-900 dark:text-white mb-4">
            Comparison Mode
          </h2>
          <div className="flex gap-4">
            <button
              onClick={() => {
                setCompareMode('line')
                setCompared(false)
                setDiffResult(null)
              }}
              className={`px-6 py-2.5 rounded-lg font-medium transition-colors ${
                compareMode === 'line'
                  ? 'bg-blue-600 text-white'
                  : 'bg-slate-200 dark:bg-slate-800 text-slate-900 dark:text-slate-100 hover:bg-slate-300 dark:hover:bg-slate-700'
              }`}
            >
              Line-by-Line
            </button>
            <button
              onClick={() => {
                setCompareMode('word')
                setCompared(false)
                setDiffResult(null)
              }}
              className={`px-6 py-2.5 rounded-lg font-medium transition-colors ${
                compareMode === 'word'
                  ? 'bg-blue-600 text-white'
                  : 'bg-slate-200 dark:bg-slate-800 text-slate-900 dark:text-slate-100 hover:bg-slate-300 dark:hover:bg-slate-700'
              }`}
            >
              Word-by-Word
            </button>
          </div>
        </div>

        {/* Cleaning Options */}
        <div className="bg-white dark:bg-slate-900 rounded-lg border border-slate-200 dark:border-slate-800 p-6 mb-6 shadow-sm">
          <h2 className="text-lg font-semibold text-slate-900 dark:text-white mb-4">
            Cleaning Options
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <label className="flex items-center gap-3 cursor-pointer">
              <Toggle checked={removeExtraLines1} onChange={setRemoveExtraLines1} />
              <span className="text-sm text-slate-700 dark:text-slate-300">
                Remove extra lines (Text 1)
              </span>
            </label>

            <label className="flex items-center gap-3 cursor-pointer">
              <Toggle checked={removeSpaces1} onChange={setRemoveSpaces1} />
              <span className="text-sm text-slate-700 dark:text-slate-300">
                Remove spaces (Text 1)
              </span>
            </label>

            <label className="flex items-center gap-3 cursor-pointer">
              <Toggle checked={removeExtraLines2} onChange={setRemoveExtraLines2} />
              <span className="text-sm text-slate-700 dark:text-slate-300">
                Remove extra lines (Text 2)
              </span>
            </label>

            <label className="flex items-center gap-3 cursor-pointer">
              <Toggle checked={removeSpaces2} onChange={setRemoveSpaces2} />
              <span className="text-sm text-slate-700 dark:text-slate-300">
                Remove spaces (Text 2)
              </span>
            </label>
          </div>
        </div>

        {/* Text Inputs */}
        <div className="bg-white dark:bg-slate-900 rounded-lg border border-slate-200 dark:border-slate-800 p-6 md:p-8 shadow-sm mb-6">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
            <div>
              <div className="flex items-center justify-between mb-3">
                <label className="block text-sm font-semibold text-slate-900 dark:text-white">
                  Text 1
                </label>
                <span className="text-xs text-slate-500 dark:text-slate-400">
                  {text1.split('\n').length} lines · {text1.length} chars
                </span>
              </div>
              <textarea
                value={text1}
                onChange={(e) => setText1(e.target.value)}
                placeholder="Paste first text here..."
                className="w-full h-80 p-4 text-sm font-mono bg-slate-50 dark:bg-slate-950 border border-slate-300 dark:border-slate-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-slate-900 dark:text-white placeholder-slate-400 dark:placeholder-slate-600 resize-none transition-colors"
              />
            </div>

            <div>
              <div className="flex items-center justify-between mb-3">
                <label className="block text-sm font-semibold text-slate-900 dark:text-white">
                  Text 2
                </label>
                <span className="text-xs text-slate-500 dark:text-slate-400">
                  {text2.split('\n').length} lines · {text2.length} chars
                </span>
              </div>
              <textarea
                value={text2}
                onChange={(e) => setText2(e.target.value)}
                placeholder="Paste second text here..."
                className="w-full h-80 p-4 text-sm font-mono bg-slate-50 dark:bg-slate-950 border border-slate-300 dark:border-slate-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-slate-900 dark:text-white placeholder-slate-400 dark:placeholder-slate-600 resize-none transition-colors"
              />
            </div>
          </div>

          {/* Buttons */}
          <div className="flex gap-3 flex-wrap">
            <button
              onClick={compareTexts}
              disabled={!text1 && !text2}
              className="px-6 py-2.5 bg-blue-600 hover:bg-blue-700 disabled:bg-slate-400 disabled:cursor-not-allowed text-white font-medium rounded-lg transition-colors shadow-sm"
            >
              Compare Texts
            </button>

            <button
              onClick={swapTexts}
              disabled={!text1 && !text2}
              className="flex items-center gap-2 px-6 py-2.5 bg-slate-200 hover:bg-slate-300 dark:bg-slate-800 dark:hover:bg-slate-700 disabled:opacity-50 disabled:cursor-not-allowed text-slate-900 dark:text-slate-100 font-medium rounded-lg transition-colors"
            >
              <ArrowRightLeft className="w-4 h-4" />
              Swap
            </button>

            {compared && (
              <button
                onClick={handleReset}
                className="flex items-center gap-2 px-6 py-2.5 bg-slate-200 hover:bg-slate-300 dark:bg-slate-800 dark:hover:bg-slate-700 text-slate-900 dark:text-slate-100 font-medium rounded-lg transition-colors"
              >
                <RotateCcw className="w-4 h-4" />
                Reset
              </button>
            )}
          </div>
        </div>

        {/* Comparison Results */}
        {compared && diffResult && (
          <div className="bg-white dark:bg-slate-900 rounded-lg border border-slate-200 dark:border-slate-800 p-6 md:p-8 shadow-sm">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-slate-900 dark:text-white">
                Comparison Results
              </h2>
              {compareMode === 'line' && selectedLines.size > 0 && (
                <div className="flex gap-2">
                  <button
                    onClick={mergeSelectedToLeft}
                    className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white text-sm font-medium rounded-lg transition-colors"
                  >
                    <ArrowLeft className="w-4 h-4" />
                    Merge {selectedLines.size} to Left
                  </button>
                  <button
                    onClick={mergeSelectedToRight}
                    className="flex items-center gap-2 px-4 py-2 bg-green-600 hover:bg-green-700 text-white text-sm font-medium rounded-lg transition-colors"
                  >
                    Merge {selectedLines.size} to Right
                    <ArrowRight className="w-4 h-4" />
                  </button>
                </div>
              )}
            </div>

            {renderComparison()}

            {/* Statistics */}
            {compareMode === 'line' && (
              <div className="mt-6 grid grid-cols-2 md:grid-cols-4 gap-4">
                <div className="bg-slate-50 dark:bg-slate-800 rounded-lg p-4">
                  <p className="text-xs font-medium text-slate-500 dark:text-slate-400 mb-1">
                    Removed
                  </p>
                  <p className="text-2xl font-bold text-red-600 dark:text-red-400">
                    {stats.removed}
                  </p>
                </div>

                <div className="bg-slate-50 dark:bg-slate-800 rounded-lg p-4">
                  <p className="text-xs font-medium text-slate-500 dark:text-slate-400 mb-1">
                    Added
                  </p>
                  <p className="text-2xl font-bold text-green-600 dark:text-green-400">
                    {stats.added}
                  </p>
                </div>

                <div className="bg-slate-50 dark:bg-slate-800 rounded-lg p-4">
                  <p className="text-xs font-medium text-slate-500 dark:text-slate-400 mb-1">
                    Modified
                  </p>
                  <p className="text-2xl font-bold text-yellow-600 dark:text-yellow-400">
                    {stats.modified}
                  </p>
                </div>

                <div className="bg-slate-50 dark:bg-slate-800 rounded-lg p-4">
                  <p className="text-xs font-medium text-slate-500 dark:text-slate-400 mb-1">
                    Identical
                  </p>
                  <p className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                    {stats.unchanged}
                  </p>
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </main>
  )
}